//const AWS = require("aws-sdk");

import { Handler } from "aws-lambda";
import { getDestination } from "./db";

export const handler: Handler = async event => {
  let response;

  const responseFallback = (response = {
    statusCode: 301,
    headers: {
      Location: "https://ibrahimcesar.cloud",
    },
  });

  if (event.httpMethod !== "GET") return null;
  // const response = {
  //   statusCode: 301,
  //   headers: {
  //     Location: "https://google.com/",
  //   },
  // };

  try {
    const hasMapped = await getDestination(event.path);
    console.log(`hasMapped: ${JSON.stringify(hasMapped)}`);
    if (hasMapped) {
      response = {
        statusCode: 301,
        headers: {
          Location: hasMapped,
        },
      };
    } else {
      response = responseFallback;
    }
  } catch (err) {
    console.error(err);
    response = responseFallback;
  }

  return response;
};
